import React, { Component } from 'react';
import { Route,BrowserRouter as Router, Link } from 'react-router-dom';
import './Dogs.css';
import Top from './Images/Top.png';
import Name from './Images/Cyrus.png';
import Filtered from './Dogs/Filtered.PNG';
import Full from './Dogs/Full.PNG';

var greeting_style = {
  fontFamily: 'Montserrat'
}

class Dogs extends Component {
  componentDidMount() {
    window.scrollTo({
      top: 0,
      left: 0,
      behavior: 'smooth'
    });
  }
  render() {
    return (
      <div style = {greeting_style}>
      <div id = "navs" >
        <img id = "name" src = {Name} alt = "Cyrus Twombly Logo"/>
        <div id = "nav">
          <h2 className = "navitem" id = "About"><Link to = "/">About</Link></h2>
          <h2 className = "navitem" id = "Projects"><Link to = "/#Project-Container">Projects</Link></h2>
          <h2 className = "navitem" id = "resume" ><a href = "https://github.com/cyrustwom">Resume</a></h2>
          <h2 className = "navitem" id = "LinkedIn"><a href = "https://linkedin.com/in/cyrus-twombly-34308812b">LinkedIn</a></h2>
          <h2 className = "navitem" id = "Github"><a href = "https://github.com/cyrustwom">Github</a></h2>
        </div>
        <h1 id = "greeting"> Cyrus Twombly</h1>
      </div>
      <div id = "bod">
        <h1 className = "title">Dog Sorter</h1>
        <p className = "bodytext">
        An assignment for my User Interface / Experience class, this dog sorting react <a className = "linktofig" href = "https://cyrustwom.github.io/Dog-Sorter/">page</a> is smooth, easy, and simple.
        Though it was a technical project, I decided to approach it with an emphasis on visual design, as I reached full functionality far before the due date.
        This project hints at who I am as a designer: a technically savvy engineer with an eye for aesthetics.
        </p>
        <img src = {Full} alt = "Image of Website with no filters or sorts" className = "Full"/>

        <p className = "bodytext">
        Above all else, I wanted this page to be enjoyable to use. It does not need to garner hours of the user's attention.
        Instead, the user should simply know, without thinking, how to use the website. I feel that with the well labeled filter and sort buttons and cute puppy cards,
        that this effect was achieved. Due to the short term nature of the project, I did not have time to go through any lo-fi iteration. See below for a filtered and differently sorted version of the page.
        </p>
        <img src = {Filtered} alt = "Filtered Website" className = "Full"/>

        <h2 className = "section"> Lessons Learned </h2>
        <p className = "bodytext" id = "bottom">
        Some peers, upon viewing my website, did not understand that the title was intended to be in a Bostonian accent.
        Additionally, the custom categories of Cuteness and Rating were confusing to some of those peers. Therefore, in my next iteration of this
        application I would add a question mark icon next to the cuteness filter to explain how it was created, and instead of labelling the rating "Rating",
        I would label it "My Rating" so as to clarify the source of the ratings. Otherwise, I have relatively few gripes with my finished product. It is a simple
        site that simply works.
        </p>
          <a href = "#navs"><img className = "Top" src = {Top} alt ="To the top button"/></a>
      </div>
      </div>
    );
  }
}

export default Dogs;
