import React, { Component } from 'react';
import { Route,BrowserRouter as Router, Link } from 'react-router-dom';
import './Proximus.css';
import Name from './Images/Cyrus.png';
import Splash from './Proximus/Splash.png'
import Overview from './Proximus/Overview.PNG';
import Top from './Images/Top.png';


var intro_style = {
  // backgroundImage: 'url('+Back2+')',
  // backgroundSize: 'cover',
  display: 'flex',
  flexDirection: 'column',
  minHeight: '100vh'
}
var greeting_style = {
  fontFamily: 'Montserrat'
}


class Proximus extends Component {
  componentDidMount() {
    window.scrollTo({
      top: 0,
      left: 0,
      behavior: 'auto'
    });
  }
  render() {
    return (
      <div style = {greeting_style}>
      <div id = "navs">
        <img id = "name" src = {Name} alt = "Cyrus Twombly Logo"/>
        <div id = "nav">
          <h2 className = "navitem" id = "About"><Link to = "/">About</Link></h2>
          <h2 className = "navitem" id = "Projects"><Link to = "/#Project-Container">Projects</Link></h2>
          <h2 className = "navitem" id = "resume" ><a href = "https://github.com/cyrustwom">Resume</a></h2>
          <h2 className = "navitem" id = "LinkedIn"><a href = "https://linkedin.com/in/cyrus-twombly-34308812b">LinkedIn</a></h2>
          <h2 className = "navitem" id = "Github"><a href = "https://github.com/cyrustwom">Github</a></h2>
        </div>
        <h1 id = "greeting"> Cyrus Twombly</h1>
      </div>
      <div id = "bod">
        <h1 className = "title"> Proximus</h1>
        <div id = "blue">
        <img id = "logo" className = "imageP" src = {Splash} alt = "Proximus Splash Screen"/>
        </div>
        <p className = "bodytext">
          A startup idea and my first UI design. This is a mockup for a location-based chat app similar to Yik Yak. It involves anonymous and identified chats, and two range settings (town and building).
        </p>
        <h2 className = "section" id = "initdesign"> Design Decisions </h2>

        <p className = "bodytext"  >
        Proximus's UI mirrors precedents set by many chat apps. There are reactions, message bubbles, and other media
        message type options available to the user. However, reactions are tracked and saved. The goal is to
        gamify messaging in a way that is addictive but unobtrusive. In this mock-up, building and town do not have popularity tabs like
        hot, top, or most recent, but that is a feature that I would like to add.
        </p>
        <div id = "grey" >
        <img id = "overv" className = "imageP" src = {Overview} alt = "Proximus Overview"/>
        </div>


        <h2 className = "section"> Lessons Learned </h2>
        <p className = "bodytext" id = "lst">
        I am not overly thrilled with the result of my efforts for Proximus.
        To the eye, the chat looks clunky, as if was a chat app from the first generation of iOS.
        Additionally, the blue of the splash screen appears a bit off to me.
        The indicator for your current page was placed right next to the burger bar, which makes it seem like a button rather than a general indication of your current location.
        However, overall the project was absolutely worth the time, as it got me interested in UI/UX design.
        </p>
        <a href = "#navs"><img className = "Top" src = {Top} alt ="To the top button"/></a>
      </div>
      </div>
    );
  }
}

export default Proximus;
