import React, { Component } from 'react';
import { Route,BrowserRouter as Router, Link } from 'react-router-dom';
import './Cct.css';
import Top from './Images/Top.png';
import Name from './Images/Cyrus.png';
import Figma from './Cct/figma.png';
import Desktop from './Cct/desktop.PNG';
import Ipad from './Cct/ipad.PNG';
import Iphone from './Cct/iphone.PNG';
import Old from './Cct/old.png';
import Balsamiq from './Cct/Balsamiq.png'
import Comparison from './Cct/Comparison.png'
import Usability from './Cct/Usability.png'
import Markup from './Cct/Markup.png'


var greeting_style = {
  fontFamily: 'Montserrat'
}


class Cct extends Component {
  componentDidMount() {
    window.scrollTo({
      top: 0,
      left: 0,
      behavior: 'auto'
    });
  }
  render() {
    return (
      <div style = {greeting_style}>
      <div id = "navs">
        <img id = "name" src = {Name} alt = "Cyrus Twombly Logo"/>
        <div id = "nav">
          <h2 className = "navitem" id = "About"><a href = "index.html">About</a></h2>
          <h2 className = "navitem" id = "Projects"><Link to = "/#Project-Container">Projects</Link></h2>
          <h2 className = "navitem" id = "resume" ><a href = "https://github.com/cyrustwom">Resume</a></h2>
          <h2 className = "navitem" id = "LinkedIn"><a href = "https://linkedin.com/in/cyrus-twombly-34308812b">LinkedIn</a></h2>
          <h2 className = "navitem" id = "Github"><a href = "https://github.com/cyrustwom">Github</a></h2>
        </div>
        <h1 id = "greeting"> Cyrus Twombly</h1>
      </div>
      <div id = "bod">
        <h1 className = "title">Cape Cod News Responsive Redesign</h1>
        <img src = {Figma} alt = "Hi-fi mockup" className = "imageC"/>

        <p className = "bodytext">
        Above is my hi-fi redesign mockup of The Cape Cod Enterprise website. Cape News is a media outlet focusing on the
        news and lifestyle of Cape Cod, MA. Their original website, shown below and available <a href = "https://www.capenews.net/" className = "linktofig">here</a>, was guilty of being a usability nightmare with an additional charge
        of being in aesthetic disarray.
        </p>
        <img src = {Old} alt = "Their Current Site" className = "imageC"/>
        <p className = "bodytext"> The colors clash, the formatting looks rushed, and there is minimal news featured for the reader to witness.
        In addition, there many fields that look like afterthoughts, such as the very top bar of white filled in part by a current weather widget with
        an icon that overflows into the splash background image of the title division.
        </p>
        <h2 className = "section" id = "initdesign"> Initial Design </h2>
        <p className = "bodytext">
        I started my redesign with low resolution prototypes in Balsamiq.
        I strove to emphasize clear directives for the user and a cleaner aesthetic.
        </p>
        <img src = {Balsamiq} alt = "Low-fi Mockups" className = "imageC"/>
        <p className = "bodytext">
        There are two columns for news on the homepage, and a side column for items that I deemed important for users to see,
        but separate in fundamental property from the left side's news. Cape News has a podcast, an event submission form, a mailing list, etc.
        Two main navigational panes exist: the title bar and the nav bar. The nav bar has largely the same content as in the original design, as I know that
        long time readers are probably used to the sections in the physical newspaper and would not appreciate a switch in organizational flow of their topics.
        The four town divisions also stayed, because Cape News produces the Falmouth Enterprise, the Bourne Enterprise, the Sandwich Enterprise, and the Mashpee Enterprise.
        From here, the hi-fi mockup you saw at the top of this page was produced.
        </p>
        <h2 className = "section" id = "usability"> Usability Comparison </h2>
        <img id = "comparison" src = {Comparison} alt = "Comparison of Hi-Fi Mockup to Original" className = "imageC"/>
        <p className = "bodytext">
        A usability comparison in the context of <a href = "https://www.usability.gov/what-and-why/usability-evaluation.html" className = "linktofig">usability.gov</a>'s criterion was performed between the original site and the hi-fi mockup.
        </p>
        <img src = {Usability} alt = "Usability criterion comparison" className = "imageC"/>
        <h2 className = "section" id = "Responsive"> Responsive Web Design</h2>
        <p className = "bodytext">
          After designing the hi-fi prototype, I converted that vision into an html/css webpage responsive to different screen sizes.
        </p>
        <img src = {Markup} alt = "Markup of Mockup" className = "imageC"/>
        <p className = "bodytext">
          I wanted to use a CSS grid layout for as small a screen as possible for this design, because the grid is a defining feature of a newspaper.
          At a certain point, this wasn't feasible, and on iPhone and lower screen widths, I switched to a signle column approach. Overall, I am happy with
          how the site looks at each distinct resolution.
        </p>
        <h3 className = "minisection">Desktop</h3>
        <img src = {Desktop} alt = "Desktop View of Homepage" className = "imageC"/>
        <h3 className = "minisection">Ipad</h3>
        <img src = {Ipad} alt = "Ipad View of Homepage" className = "imageC" id = "ipad"/>
        <h3 className = "minisection">Iphone</h3>
        <img src = {Iphone} alt = "Iphone View of Homepage" className = "imageC" id = "iphone"/>
        <h2 className = "section"> Lessons Learned</h2>
        <p className = "bodytext" id = "bottom">
        I used CSS Grid to format the columns of the page. This turned out to be a mistake.
        CSS Grid made it very difficult to align the text properly within the grid divisions. At different resolutions, the text moved easily.
        In the future I will fix this issue by using vw, vh, fr, etc for every measurement of space, and using Flexbox whenever possible.
        In terms of design, I am happy with the finished product, except for the grid formatting. I believe that the site looks like a newspaper, and
        that it is an attractive newspaper at that. If I were to improve on the hi-fi mockup and website appearance in any way, I would like to get a bit more Cape personalization into the design.
        </p>
        <a href = "#navs"><img className = "Top" src = {Top} alt ="To the top button"/></a>

      </div>
      </div>
    );
  }
}

export default Cct;
